@import "../../../../assets/styles/app";

.confirmedGrid {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  padding: 6px;
  overflow-y: auto;

  @include desktop {
    padding: $k * 6px;
  }

  .card {
    position: relative;
    padding: 12px 11px 60px;
    border: 1px solid $color_lines;
    margin: 6px;
    width: 162px;

    @include desktop {
      padding: $k * 12px $k * 11px $k * 60px;
      margin: $k * 6px;
      width: $k * 218px;
    }

    .imgWrapper {
      position: relative;
      width: 138px;
      height: 138px;

      @include desktop {
        width: $k * 194px;
        height: $k * 194px;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
      }

      .addToFavoritesBtn {
        @include button;
        @include centerRow;
        position: absolute;
        top: 8px;
        right: 8px;
        border: 1px solid $color_lines;
        width: 36px;
        height: 36px;
        backdrop-filter: blur(20px);
        background: $color_bg_and_text;

        @include desktop {
          top: $k * 4px;
          right: $k * 4px;
          width: $k * 36px;
          height: $k * 36px;
        }

        svg {
          width: 16px;
          height: 16px;

          @include desktop {
            width: $k * 16px;
            height: $k * 16px;
          }
        }
      }
    }

    .name {
      margin-top: 12px;
      font-weight: 400;
      font-size: 12px;
      line-height: 140%;
      color: $color_main_100;

      @include desktop {
        margin-top: $k * 12px;
        font-size: $k * 12px;
      }
    }

    .info {
      margin-top: 8px;
      display: flex;
      align-items: center;

      @include desktop {
        margin-top: $k * 8px;
      }

      p {
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: $color_body_50;

        @include desktop {
          font-size: $k * 12px;
        }
      }

      & > svg {
        margin-left: 4px;
        width: 12px;
        height: 12px;

        @include desktop {
          margin-left: $k * 4px;
          width: $k * 12px;
          height: $k * 12px;
        }
      }

      a {
        @include centerRow;
        margin-left: 4px;
        width: 12px;
        height: 12px;

        @include desktop {
          margin-left: $k * 4px;
          width: $k * 12px;
          height: $k * 12px;
        }

        svg {
          width: 12px;
          height: 12px;

          @include desktop {
            width: $k * 12px;
            height: $k * 12px;
          }
        }
      }
    }

    .price {
      margin-top: 8px;
      display: flex;
      align-items: center;

      @include desktop {
        margin-top: $k * 8px;
      }

      svg {

        @include desktop {
          width: $k * 16px;
          height: $k * 16px;
        }

        path {
          fill: $color_main;
          fill-opacity: 1;
        }
      }

      p {
        margin-left: 4px;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: $color_main;

        @include desktop {
          margin-left: $k * 4px;
          font-size: $k * 12px;

        }
      }

    }

    .followers {
      margin-top: 12px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include desktop {
        margin-top: $k * 12px;
      }

      .label {
        font-size: 12px;
        line-height: 140%;
        color: $color_body_50;

        @include desktop {
          font-size: $k * 12px;
        }
      }

      .value {
        padding: 4px 8px;
        border: 1px solid $color_lines;
        display: flex;
        align-items: center;

        @include desktop {
          padding: $k * 4px $k * 8px;
        }

        svg {
          width: 12px;
          height: 12px;

          @include desktop {
            width: $k * 12px;
            height: $k * 12px;
          }
        }

        span {
          margin-left: 4px;
          font-size: 12px;
          line-height: 140%;
          text-align: center;
          color: $color_main_100;

          @include desktop {
            margin-left: $k * 4px;
            font-size: $k * 12px;
          }
        }
      }
    }



    .prices {
      margin-top: 8px;

      @include desktop {
        margin-top: $k * 8px;
      }

      .pricesRow {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:not(:first-child) {
          margin-top: 8px;

          @include desktop {
            margin-top: $k * 8px;
          }
        }

        .label {
          font-weight: 400;
          font-size: 12px;
          line-height: 140%;
          color: $color_body_50;

          @include desktop {
            font-size: $k * 12px;
          }
        }

        .value {
          display: flex;
          align-items: center;

          span {
            margin-left: 4px;
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            text-align: right;
            color: $color_main_100;

            @include desktop {
              margin-left: $k * 4px;
              font-size: $k * 12px;
            }
          }
        }
      }
    }


    .statistics {
      margin-top: 12px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);

      @include desktop {
        margin-top: $k * 12px;
      }

      .statisticsItem {
        @include centerRow;

        &:not(:first-child) {
          border-left: 1px solid $color_lines;
        }

        svg {
          path {
            fill: #FFF;
            fill-opacity: 0.5;
          }
        }

        span {
          margin-left: 4px;
          font-weight: 400;
          font-size: 12px;
          line-height: 140%;
          color: $color_body_50;

          @include desktop {
            margin-left: $k * 4px;
            font-size: $k * 12px;
          }
        }
      }
    }

    .bottom {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      display: flex;
      height: 36px;

      @include desktop {
        height: $k * 36px;
      }

      .quickPledgeBtn {
        flex-grow: 1;
        height: 100%;
        width: 100%;
        border-top: 1px solid $color_lines;
        border-right: 1px solid $color_lines;
      }

      .shareBtn {
        flex-shrink: 0;
        width: 36px;
        height: 100%;
        border-top: 1px solid $color_lines;

        @include desktop {
          width: $k * 36px;
        }
      }
    }

  }
}