@import "../../../../assets/styles/app";

.unconfirmedGrid {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 6px;
  overflow-y: auto;

  @include desktop {
    padding: $k * 6px;
  }

  .card {
    position: relative;
    padding: 12px 11px 60px;
    border: 1px solid $color_lines;
    margin: 6px;
    width: 162px;

    @include desktop {
      width: $k * 218px;
      padding: $k * 12px $k * 11px $k * 60px;
      margin: $k * 6px;
    }

    .imgWrapper {
      position: relative;
      width: 138px;
      height: 138px;

      @include desktop {
        width: $k * 194px;
        height: $k * 194px;
      }

      img {
        display: block;
        width: 100%;
        height: 100%;
      }

    }

    .nameWrapper {
      margin-top: 12px;
      display: flex;
      align-items: center;

      @include desktop {
        margin-top: $k * 12px;
      }

      p {
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: $color_main_100;

        @include desktop {
          font-size: $k * 12px;
        }
      }

      a {
        @include centerRow;
        width: 12px;
        height: 12px;
        margin-left: 4px;

        @include desktop {
          width: $k * 12px;
          height: $k * 12px;
          margin-left: $k * 4px;
        }

        svg {
          width: 12px;
          height: 12px;

          @include desktop {
            width: $k * 12px;
            height: $k * 12px;
          }
        }
      }
    }

    .pledgeAmount {
      margin-top: 8px;
      display: flex;
      align-items: center;

      @include desktop {
        margin-top: $k * 8px;
      }

      svg {
        @include desktop {
          width: $k * 16px;
          height: $k * 16px;
        }

        path {
          fill: $color_main;
          fill-opacity: 1;
        }
      }

      p {
        margin-left: 4px;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: $color_main;

        @include desktop {
          margin-left: $k * 4px;
          font-size: $k * 12px;
        }
      }

    }

    .followers {
      margin-top: 12px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include desktop {
        margin-top: $k * 12px;
      }

      .label {
        font-size: 12px;
        line-height: 140%;
        color: $color_body_50;

        @include desktop {
          font-size: $k * 12px;
        }
      }

      .value {
        padding: 4px 8px;
        border: 1px solid $color_lines;
        display: flex;
        align-items: center;

        @include desktop {
          padding: $k * 4px $k * 8px;
        }

        svg {
          width: 12px;
          height: 12px;

          @include desktop {
            width: $k * 12px;
            height: $k * 12px;
          }
        }

        span {
          margin-left: 4px;
          font-size: 12px;
          line-height: 140%;
          text-align: center;
          color: $color_main_100;

          @include desktop {
            margin-left: $k * 4px;
            font-size: $k * 12px;
          }
        }
      }




    }

    .twitterJoined {
      margin-top: 6px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include desktop {
        margin-top: $k * 6px;
      }

      .label {
        font-size: 12px;
        line-height: 140%;
        color: $color_body_50;

        @include desktop {
          font-size: $k * 12px;
        }
      }

      .value {
        font-size: 12px;
        line-height: 140%;
        text-align: center;
        color: $color_main_100;

        @include desktop {
          font-size: $k * 12px;
        }
      }
    }

    .pledge {
      margin-top: 8px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include desktop {
        margin-top: $k * 8px;
      }

      .label {
        font-size: 12px;
        line-height: 140%;
        color: $color_body_50;

        @include desktop {
          font-size: $k * 12px;
        }
      }

      .value {
        display: flex;
        align-items: center;

        svg {
          width: 16px;
          height: 16px;

          path {
            fill: #FFF;
            fill-opacity: 0.5;
          }

          @include desktop {
            width: $k * 16px;
            height: $k * 16px;
          }
        }

        span {
          margin-left: 4px;
          font-size: 12px;
          line-height: 140%;
          text-align: right;
          color: $color_main_100;

          @include desktop {
            margin-left: $k * 4px;
            font-size: $k * 12px;
          }
        }
      }
    }


    .statistics {
      margin-top: 20px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);

      @include desktop {
        margin-top: $k * 20px;
      }

      .statisticsItem {
        @include centerRow;

        &:not(:first-child) {
          border-left: 1px solid $color_lines;
        }

        svg {

          @include desktop {
            width: $k * 12px;
            height: $k * 12px;
          }

          path {
            fill: #FFF;
            fill-opacity: 0.5;
          }
        }

        span {
          margin-left: 4px;
          font-weight: 400;
          font-size: 12px;
          line-height: 140%;
          color: $color_body_50;

          @include desktop {
            margin-left: $k * 4px;
            font-size: $k * 12px;
          }
        }
      }
    }

    .bottom {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      display: flex;
      height: 36px;

      @include desktop {
        height: $k * 36px;
      }

      .quickPledgeBtn {
        flex-grow: 1;
        height: 100%;
        width: 100%;
        border-top: 1px solid $color_lines;
        border-right: 1px solid $color_lines;
      }

      .shareBtn {
        flex-shrink: 0;
        width: 36px;
        height: 100%;
        border-top: 1px solid $color_lines;

        @include desktop {
          width: $k * 36px;
        }
      }
    }

  }

}