@import "../../../assets/styles/app";

.tweet {
  @include centerColumn;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $color_pop_up_bg;
  backdrop-filter: blur(5px);

  .card {
    width: 100%;
    max-width: 440px;
    border: 1px solid $color_lines;
    background-color: $color_bg_and_text;

    @include desktop {
      max-width: $k * 440px;
    }

    .cardHeader {
      height: 36px;
      display: flex;
      border-bottom: 1px solid $color_lines;

      @include desktop {
        height: $k * 36px;
      }

      .cardHeaderLeft {
        flex-grow: 1;
        display: flex;
        align-items: center;
        padding-left: 12px;

        @include desktop {
          padding-left: $k * 12px;
        }

        svg {

          path {
            fill-opacity: 1;
          }

          @include desktop {
            width: $k * 16px;
            height: $k * 16px;
          }

        }

        span {
          margin-left: 8px;
          font-weight: 700;
          font-size: 12px;
          line-height: 140%;
          text-transform: uppercase;
          color: $color_main_100;

          @include desktop {
            margin-left: $k * 8px;
            font-size: $k * 12px;
          }
        }
      }

      .closeBtn {
        flex-shrink: 0;
        width: 36px;
        border-left: 1px solid $color_lines;

        @include desktop {
          width: $k * 36px;
        }

        svg {
          @include desktop {
            width: $k * 16px;
            height: $k * 16px;
          }
        }
      }
    }

    .body {
      padding: 12px;

      @include desktop {
        padding: $k * 12px;
      }

      p {

        span {
          font-size: 12px;
          line-height: 140%;
          color: $color_body_50;

          &:nth-child(2) {
            color: $color_main_100;
          }
        }

        &:nth-child(3) {
          span {
            &:nth-child(2) {
              color: $color_main;
            }
          }
        }
      }
    }

    .btn {
      width: 100%;
      height: 48px;

      @include desktop {
        height: $k * 48px;
      }
    }
  }
}